.Gallery_sectionContainer__R0gCh {
    margin: 0;
    /* min-height: 100vh; */
    background-color: #f6f4ef;
    font-family: sans-serif;
    display: flex;
    justify-content: center;
    align-items: center;
}

.Gallery_container__25wKn {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 10vmin;
    overflow: hidden;
    transform: skew(8deg);
}
.Gallery_container__25wKn .Gallery_card__1467I {
	flex: 1 1;
	transition: all 1s ease-in-out;
	height: 75vmin;
	position: relative;
}
.Gallery_container__25wKn .Gallery_card__1467I .Gallery_cardHead__15UKQ {
	color: black;
	background: #8631d5d1;
	padding: 0.5em;
	transform: rotate(-90deg);
	transform-origin: 0% 0%;
	transition: all 0.5s ease-in-out;
	min-width: 100%;
	text-align: center;
	position: absolute;
	bottom: 0;
	left: 0;
	font-size: 1em;
	white-space: nowrap;
}

.Gallery_container__25wKn .Gallery_card__1467I:hover {
	flex-grow: 10;
}

.Gallery_container__25wKn .Gallery_card__1467I:hover .Gallery_cardHead__15UKQ {
	text-align: center;
	top: calc(100% - 2em);
	color: white;
	background: rgba(0, 0, 0, 0.5);
	font-size: 2em;
	transform: rotate(0deg) skew(-5deg);
}
.Gallery_container__25wKn .Gallery_card__1467I img {
	width: 100%;
	height: 100%;
	object-fit: cover;
	transition: all 1s ease-in-out;
}

.Gallery_container__25wKn .Gallery_card__1467I:not(:nth-child(5)) {
	margin-right: 1em;
}

